// import { BadgeCheckIcon, ChipIcon } from "@heroicons/react/solid";
import React from "react";
import { skills } from "../data";

export default function Skills() {
  return (
    <section id="skills">
       <h1 className="md:text-6xl text-4xl font-medium text-center title-font mb-4 ">
       Skills &amp; Technologies
      </h1>
      <div className="w-full px-5 py-10 mx-auto">
         
        <div className="flex flex-wrap lg:w-4/5 mx-auto w-full">
          {skills.filter(skill => skill.type = 'A').map((skillA) => (
            <div   className="p-2 sm:w-1/2 w-full">
              <div className="bg-white shadow-xl rounded flex p-4 h-full items-center">
                {/* { <BadgeCheckIcon className="text-red-400 w-6 h-6 flex-shrink-0 mr-4" /> } */}
                <img
                  alt="icon"
                  className="w-6 h-6 lex-shrink-0 mr-4"
                  src={skillA.icon}
                />
                <span className="title-font font-medium ">
                  {skillA.name}
                </span>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}


